  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppins";
}

body {
  overflow-x: hidden;
  background-color: #323232;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 6rem;
  background-color: #091523;
  width: 100%;
  height: 7em;
  position: fixed;
  z-index: 99999;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
  opacity: 96%;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 9rem;
  font-size: 1.3em;
  font-weight: 500;
}

.nav-links li a {
  text-decoration: none;
  color: white;
  transition: ease-in-out 0.2s;
}

.nav-links li a:hover {
  text-decoration: none;
  color: #f23b26;
}

.logo {
  width: 90px;
  cursor: pointer;
}

/* burger */
.burger {
  position: relative;
  width: 40px;
  height: 30px;
  background: transparent;
  cursor: pointer;
  display: none;
}

.burger input {
  display: none;
}

.burger span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background: rgb(255, 255, 255);
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

.burger span:nth-of-type(1) {
  top: 0px;
  transform-origin: left center;
}

.burger span:nth-of-type(2) {
  top: 50%;
  transform: translateY(-50%);
  transform-origin: left center;
}

.burger span:nth-of-type(3) {
  top: 100%;
  transform-origin: left center;
  transform: translateY(-100%);
}

.burger input:checked ~ span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 0px;
  left: 5px;
}

.burger input:checked ~ span:nth-of-type(2) {
  width: 0%;
  opacity: 0;
}

.burger input:checked ~ span:nth-of-type(3) {
  transform: rotate(-45deg);
  top: 28px;
  left: 5px;
}

.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(70%);
}

.hero-container {
  height: fit-content;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.hero-content {
  position: absolute;
  color: white;
  z-index: 2;
}

.hero-content h2 {
  font-size: 3.5rem;
  color: #f23b26;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  margin-bottom: 0.5rem;
}

.container .title {
  font-size: 2.5rem;
  color: white;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  margin: 1em auto;
  text-align: center;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-gap: 20px;
  justify-items: center;
  margin-bottom: 2rem;
}

.card {
  width: 400px;
  height: 500px;
  background-image: linear-gradient(163deg, #ff9913 0%, #ff0000 100%);
  border-radius: 20px;
  transition: all 0.3s;
}

.card2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 400px;
  height: 500px;
  background-color: #091523;
  border-radius: 20px;
  transition: all 0.2s;
  color: white;
}

.card2:hover {
  transform: scale(0.98);
  border-radius: 20px;
  cursor: pointer;
}

.card:hover {
  box-shadow: 0px 0px 30px 1px #bf7009;
}

.image {
  width: 300px;
  margin-bottom: 0.2em;
  border-radius: 20px;
  height: 180px;
  object-fit: cover;
  transition: all 0.3s;
}

.image:hover {
  box-shadow: 0px 0px 30px 1px #bf7009;
  transform: scale(1.1);
}

.name {
  font-size: 1.1em;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-bottom: 0.2rem;
  color: #fd5e4c;
}

.city {
  margin-bottom: 0.2em;
  font-weight: 400;
  letter-spacing: 0.1em;
}

.description {
  margin: 1em 1em;
  max-height: 150px;
  color: white;
  font-size: 1.2em;
  font-weight: 500;
  overflow-y: auto;
  text-align: left;
  transition: all 0.3s;
  padding-right: 7px;
}

.description::-webkit-scrollbar {
  width: 6px;
  overflow: hidden;
}

.description::-webkit-scrollbar-thumb {
  background-color: #fb412c;
  border-radius: 10px;
  display: none;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.description::-webkit-scrollbar-track {
  background-color: #650d04;
  border-radius: 10px;
  display: none;
  transition: opacity 0.3s ease-in-out;
}

.description:hover::-webkit-scrollbar,
.description:hover::-webkit-scrollbar-thumb,
.description:hover::-webkit-scrollbar-track {
  display: block;
}

.rating {
  margin-top: 0.2em;
}

footer {
  background-color: #091523;
  color: white;
  text-align: center;
  padding: 0.1em;
  font-size: 1em;
  font-weight: 500;
  letter-spacing: 0.1em;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.5);
  width: 100%;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 1000000;
  transition: top 0.3s;
  text-decoration: none;
}

.skip-link:focus {
  top: 0;
}
